ul {
  margin: 0;
}

li {
  list-style: none;
}
li a {
  display: inline-block;
}

button {
  border: none;
  background: none;
}

.container {
  max-width: 1200px;
  margin: auto;
}

.card {
  border: 1px solid #ffffff;
  border-radius: 5px;
}
.card__image {
  width: 300px;
  height: 300px;
}
.card__content .content-header__primary {
  color: black;
}
.card__content .content-header__secondary {
  color: gray;
}
.card__content--deatails {
  line-height: 16px;
}
.card__content .btn {
  border-radius: 5px;
}

.section__head {
  line-height: 1;
  position: relative;
}
.section__head::before {
  content: "";
  position: absolute;
  top: 50%;
  height: 1px;
  width: 120px;
  background-color: black;
}

.carousel {
  position: relative;
}
.carousel__btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.carousel__btn--next {
  right: 0;
  z-index: 100;
}
.carousel__btn--prev {
  left: 0;
  z-index: 100;
}
.carousel__images {
  position: relative;
  width: 100%;
  max-height: 100vh;
}
.carousel__images--child {
  width: 100%;
  object-fit: cover;
  max-height: 100vh;
}

.nav {
  position: relative;
  z-index: 100;
  background-color: black;
}
.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
}
.nav-container__utlities {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.nav-container__utlities .toggler-btn {
  display: none;
}
.nav-container__list {
  display: flex;
}
.nav-container__list li a {
  color: white;
  padding: 1.5rem 0.5rem;
}

.carousel {
  position: relative;
  top: 0;
  left: 0;
}
.carousel__images {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}